import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import { Grid, Typography, Button, TextField, Link } from "@mui/material";
import * as yup from "yup";
import { makeStyles } from "@mui/styles";
import ArrowForwardRoundedIcon from "@mui/icons-material/ArrowForwardRounded";
import { useFormik } from "formik";
import { ReactComponent as GoogleIcon } from "assets/GoogleIcon.svg";
import { useNavigate } from "react-router";
import clsx from "clsx";
import moment from "moment";
import { AI_signup } from "API/userAPI";
import {
  useAIEventHandler,
  useInstance
} from "easy-redux-http-call"
import { saveJWTToken } from "utils";

const useStyles = makeStyles((theme) => ({
  dmSignupRoot: {
    padding: 15,
    height: "80%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  dmSignupContentContainer: {
    width: "100%",
    "&>h4": {
      fontWeight: 600,
    },
    "&>p": {
      color: theme.palette.grey[500],
      marginTop: 5,
    },
  },
  dmLoginForm: {
    marginTop: 10,
    "&>div": {
      marginTop: 10,
    },
    "&>button": {
      marginTop: 10,
      justifyContent: "space-between",
    },
  },
  dmOrPartition: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    margin: "10px 0px",
    "&:before, &:after": {
      content: '""',
      display: "block",
      width: "100%",
      height: 1,
      backgroundColor: theme.palette.grey[300],
      margin: "0px 3px",
    },
  },
  dmSignUpWithGoogleButton: {
    justifyContent: "flex-start !important",
    background: `${theme.palette.grey[100]} !important`,
    "&>svg": {
      marginRight: 15,
    },
  },
  dmLoginAgain: {
    marginTop: "3px !important",
    textAlign: "right",
  },
}));

const passwordRules = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{5,}$/;
const signupSchema = yup.object().shape({
  name: yup.string().required("Required"),
  email: yup.string().email("Invalid email").required("Required"),
  password: yup
    .string("Required")
    .required("Password is required")
    .matches(passwordRules, { message: "Please create a stronger password" }),
  birthdate: yup.date(),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref("password"), null], "Passwords must match")
    .required("Password is required"),
});

function Signup() {
  const classes = useStyles();
  const navigate = useNavigate();

  const handleSignup = (values, actions) => {
    AI_signup.call({
      params: values
    })
  };

  const signupState = useInstance(AI_signup)

  useAIEventHandler(
    AI_signup,
    {
      onSuccess: (res) => {
        let token = res?.data?.token
        if (token) {
          saveJWTToken(res.data.token)
          navigate("/home")
        }
      },
      onError: (res) => {
        setErrors({
          password: res.error.message
        })
      }
    })

  const {
    values,
    handleChange,
    handleBlur,
    errors,
    touched,
    setErrors,
    handleSubmit,
  } = useFormik({
    initialValues:
    {
      name: "",
      email: "",
      password: "",
      birthdate: moment(new Date()).format("yyyy-MM-DDTHH:mm"),
      confirmPassword: ""
    },
    validationSchema: signupSchema,
    onSubmit: handleSignup,
  });


  const generateFormikProps = (key) => {
    return {
      fullWidth: true,
      name: key,
      value: values[key],
      onChange: handleChange,
      onBlur: handleBlur,
      helperText: touched[key] && errors[key],
      error: touched[key] && errors[key],
    };
  };

  return (
    <HeaderWrapper>
      <Grid className={classes.dmSignupRoot}>
        <Grid className={classes.dmSignupContentContainer}>
          <Typography variant="h4"> Signup </Typography>
          <Typography variant="body2"> Please Sign up to continue </Typography>
          <form className={classes.dmLoginForm} onSubmit={handleSubmit}>
            <TextField
              {...generateFormikProps("name")}
              placeholder="Name"
              label="Name"
            />
            <TextField
              {...generateFormikProps("email")}
              placeholder="Email"
              label="Email"
            />
            <TextField
              {...generateFormikProps("birthdate")}
              type="datetime-local"
              placeholder="Birthdate"
              label="Birthdate"
            />
            <TextField
              {...generateFormikProps("password")}
              placeholder="Password"
              label="Password"
            />
            <TextField
              {...generateFormikProps("confirmPassword")}
              placeholder="Confirm Password"
              label="Confirm Password"
            />
            <Button
              fullWidth
              endIcon={<ArrowForwardRoundedIcon />}
              type="submit"
              size="large"
              disabled={signupState.loading}
            >
              Sign Up
            </Button>
          </form>
          <Typography variant="subtitle2" className={classes.dmLoginAgain}>
            Already have account?&nbsp;
            <Link underline="hover" variant="subtitle2" href="/login">
              Login
            </Link>
          </Typography>
          <Grid className={classes.dmOrPartition}>
            <Typography> OR </Typography>
          </Grid>
          <Button
            fullWidth
            variant="contained"
            className={
              clsx(classes.dmSignUpWithGoogleButton,
                "comming-soon"
              )
            }
            size="large"
          >
            <GoogleIcon width="35" height="35" />
            Continue with Google
          </Button>
        </Grid>
      </Grid>
    </HeaderWrapper>
  );
}

export default Signup;
